.CartMenuItem {
  display: flex;
  cursor: pointer;
}

.CartMenuItem_image {
  height: 75px;
  width: 75px;
}
.CartMenuItem_info {
  /* flex-grow: 1; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: auto;
}
.CartMenuItem_control {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
  padding-right: 10px;
}
.CartMenuItem_title {
  font-size: larger;
  font-weight: 600;
}
.CartMenuItem_description {
  font-size: medium;
  font-weight: 400;
  color: #444;
}
